import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import * as http from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { map, catchError } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { resolveDefinition } from '@angular/core/src/view/util';


@Injectable()
export class WeatherServiceProvider {
  apiKey = '040fb563540636c411bc57239d264d34';
  baseURL = "https://api.openweathermap.org/data/2.5/weather?q="
  url;
  place :string ="";
  type: string = "";
  icon: string = "";
  temperature: string = "";
  item: any;

  constructor(public http: HttpClient) {
    console.log('Hello WeatherServiceProvider Provider');
  }

  getTemperature(city){
    city= 'cleveland'
        
    this.url = this.baseURL + city + '&appid=' + this.apiKey;
    console.log(this.url);
    return this.http.get(this.url).subscribe(data => {
    var obj = <any>data
      this.temperature = (parseFloat(obj.main.temp)-273.15).toFixed(2).toString()+ "Degrees Celecius";
      this.place = obj.name;
      this.type =obj.weather[0].main;
      this.icon = 'http://openweathermap.org/img/w/'+obj.weather[0].icon+".png";
    }
  )
}}  


